import React, { useState } from 'react';
import { Box, TextField, Button, Typography } from '@mui/material';

const SavingsGoalCalculator = () => {
  const [goalAmount, setGoalAmount] = useState('');
  const [monthlySavings, setMonthlySavings] = useState('');
  const [interestRate, setInterestRate] = useState('');
  const [years, setYears] = useState(null);

  const calculateYears = () => {
    const rate = interestRate / 100 / 12;
    const months = Math.log(1 + (goalAmount * rate) / monthlySavings) / Math.log(1 + rate);
    const calculatedYears = months / 12;
    setYears(calculatedYears);
  };

  const clearFields = () => {
    setGoalAmount('');
    setMonthlySavings('');
    setInterestRate('');
    setYears(null);
  };

  return (
    <Box sx={{ padding: 2, backgroundColor: 'background.default', color: 'text.primary' }}>
      <Typography variant="h4" gutterBottom>
        Savings Goal Calculator
      </Typography>
      <TextField
        label="Goal Amount"
        variant="outlined"
        fullWidth
        value={goalAmount}
        onChange={(e) => setGoalAmount(e.target.value)}
        type="number"
        sx={{ marginBottom: 2 }}
      />
      <TextField
        label="Monthly Savings"
        variant="outlined"
        fullWidth
        value={monthlySavings}
        onChange={(e) => setMonthlySavings(e.target.value)}
        type="number"
        sx={{ marginBottom: 2 }}
      />
      <TextField
        label="Annual Interest Rate (%)"
        variant="outlined"
        fullWidth
        value={interestRate}
        onChange={(e) => setInterestRate(e.target.value)}
        type="number"
        sx={{ marginBottom: 2 }}
      />
      <Button variant="contained" color="primary" onClick={calculateYears} sx={{ marginRight: 2, marginBottom: 2 }}>
        Calculate Years
      </Button>
      <Button variant="contained" color="secondary" onClick={clearFields} sx={{ marginBottom: 2 }}>
        Clear
      </Button>
      {years !== null && (
        <Typography variant="h6" gutterBottom>
          Years to reach your goal: {years.toFixed(2)}
        </Typography>
      )}
    </Box>
  );
};

export default SavingsGoalCalculator;
