import React, { useState } from 'react';
import { Box, Typography, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from '@mui/material';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

const data = [
  { percentage: 1, rule72: 72.00, rule69_3: 69.30, rule69: 69.00, actual: 69.66 },
  { percentage: 2, rule72: 36.00, rule69_3: 34.65, rule69: 34.50, actual: 35.00 },
  { percentage: 3, rule72: 24.00, rule69_3: 23.10, rule69: 23.00, actual: 23.45 },
  { percentage: 4, rule72: 18.00, rule69_3: 17.33, rule69: 17.25, actual: 17.67 },
  { percentage: 5, rule72: 14.40, rule69_3: 13.86, rule69: 13.80, actual: 14.21 },
  { percentage: 10, rule72: 7.20, rule69_3: 6.93, rule69: 6.90, actual: 7.27 },
  { percentage: 20, rule72: 3.60, rule69_3: 3.47, rule69: 3.45, actual: 3.80 },
  { percentage: 30, rule72: 2.40, rule69_3: 2.31, rule69: 2.30, actual: 2.64 },
  { percentage: 50, rule72: 1.44, rule69_3: 1.39, rule69: 1.38, actual: 1.71 },
  { percentage: 75, rule72: 0.96, rule69_3: 0.92, rule69: 0.92, actual: 1.24 },
  { percentage: 100, rule72: 0.72, rule69_3: 0.69, rule69: 0.69, actual: 1.00 },
];

const GlossaryChart = () => {
  const [highlightedRow, setHighlightedRow] = useState(null);

  const handleRowHover = (percentage) => {
    setHighlightedRow(percentage);
  };

  return (
    <Box sx={{ padding: 2, backgroundColor: 'background.default', color: 'text.primary' }}>
      <Typography variant="h4" gutterBottom>
        Rule of 72 Comparison
      </Typography>
      <TableContainer component={Paper} sx={{ marginBottom: 4 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Annual Interest Rate</TableCell>
              <TableCell>The Rule of 72</TableCell>
              <TableCell>The Rule of 69.3</TableCell>
              <TableCell>The Rule of 69</TableCell>
              <TableCell>Actual Number of Years</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data.map((row) => (
              <TableRow
                key={row.percentage}
                sx={highlightedRow === row.percentage ? { backgroundColor: 'lightblue' } : {}}
                onMouseEnter={() => handleRowHover(row.percentage)}
                onMouseLeave={() => handleRowHover(null)}
              >
                <TableCell>{row.percentage}%</TableCell>
                <TableCell>{row.rule72}</TableCell>
                <TableCell>{row.rule69_3}</TableCell>
                <TableCell>{row.rule69}</TableCell>
                <TableCell>{row.actual}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Typography variant="h5" gutterBottom>
        Visual Representation
      </Typography>
      <ResponsiveContainer width="100%" height={400}>
        <LineChart data={data} margin={{ top: 20, right: 30, left: 20, bottom: 5 }}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="percentage" label={{ value: 'Annual Interest Rate (%)', position: 'insideBottomRight', offset: -10 }} />
          <YAxis label={{ value: 'Years', angle: -90, position: 'insideLeft' }} />
          <Tooltip />
          <Legend />
          <Line type="monotone" dataKey="rule72" stroke="#8884d8" />
          <Line type="monotone" dataKey="rule69_3" stroke="#82ca9d" />
          <Line type="monotone" dataKey="rule69" stroke="#ffc658" />
          <Line type="monotone" dataKey="actual" stroke="#ff7300" />
        </LineChart>
      </ResponsiveContainer>
    </Box>
  );
};

export default GlossaryChart;
